import { CommonModule } from '@angular/common';
import { NgModule, ModuleWithProviders } from '@angular/core';
import { IvtHttpClientModule } from '@ivt/http-client';
import { IvtSecurityConfig } from './config';
import { IvtSecurityService } from './service/security.service';
import { IvtStorageService } from './service/storage.service';
import { Tenant } from './model/tenant';
import { User } from './model/user';
import { IvtSecurityAbstractService } from './service/security.abstract.service';
import { UserIf } from './model/user.interface';
import { IvtSecurityInterceptor } from './service/security.interceptor';
import { HTTP_INTERCEPTORS } from '@angular/common/http';

// Export module's public API
export { IvtSecurityConfig } from './config';
export { IvtSecurityService } from './service/security.service';
export { IvtStorageService } from './service/storage.service';
export { Tenant } from './model/tenant';
export { User } from './model/user';
export { UserIf } from './model/user.interface';
export { IvtSecurityAbstractService } from './service/security.abstract.service';
export { IvtSecurityInterceptor } from './service/security.interceptor';

@NgModule({
  imports: [
    CommonModule,
    IvtHttpClientModule
  ],
  exports: [],
  declarations: []
})
export class IvtSecurityModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: IvtSecurityModule,
      providers: [
        IvtSecurityConfig,
        IvtSecurityService,
        IvtStorageService,
        { provide: HTTP_INTERCEPTORS, useClass: IvtSecurityInterceptor, multi: true }
      ]
    };
  }
}
