import { Observable } from 'rxjs/Observable';
import {
    HttpInterceptor, HttpRequest, HttpHandler,
    HttpSentEvent, HttpHeaderResponse, HttpProgressEvent,
    HttpResponse, HttpUserEvent, HttpErrorResponse
} from '@angular/common/http';

import { Injectable } from '@angular/core';
import { catchError } from 'rxjs/operators';
import { IvtSecurityConfig } from '../config';

@Injectable()
export class IvtSecurityInterceptor implements HttpInterceptor {

    constructor(protected config: IvtSecurityConfig) {
    }
    intercept(req: HttpRequest<any>, next: HttpHandler):
        Observable<HttpSentEvent | HttpHeaderResponse | HttpProgressEvent | HttpResponse<any> | HttpUserEvent<any>> {
        return next.handle(req).pipe(catchError(err => {
            if (err instanceof HttpErrorResponse) {
                if (err.status === 401) {
                    this.config.onUnAuthorized(err);
                }
            }
            return Observable.throw(err);
        }));
    }

}
