import { HttpClient } from '@angular/common/http';
import { IvtStorageService } from './storage.service';
import { Observable } from 'rxjs/Observable';
import { IvtSecurityConfig } from '../config';
import { APIResponse, APICode } from '@ivt/http-client';
import 'rxjs/add/operator/map';
import { UserIf } from '../model/user.interface';

export class IvtSecurityAbstractService<T extends UserIf> {

    user: T;
    constructor(protected http: HttpClient, protected db: IvtStorageService, protected config: IvtSecurityConfig) {
        this.loadUserCredentials();
    }

    protected loadUserCredentials() {
        this.useCredentials(this.db.get('user'));
    }

    protected useCredentials(user: T) {
        if (user) {
            this.user = user;
        }
    }
    protected destroyUserCredentials() {
        this.db.remove('user');
        this.user = null;
    }

    protected storeUserCredentials(user: T) {
        if (this.config.rememberLogin === true) {
            this.db.save('user', user);
        }
        this.useCredentials(user);
    }
    protected afterLogin(res: APIResponse) {
        if (res.code === APICode.Success) {
            this.storeUserCredentials(res.data);
        }
    }
    login(loginModel: any): Observable<APIResponse> {
        return this.http.post(this.config.apiUrl, loginModel, { params: { auth: 'false' } })
            .map(res => {
                const response = <APIResponse>(res);
                this.afterLogin(response);
                return response;
            })
    }

    logout() {
        // TODO
        this.destroyUserCredentials();
    }
    getUser(): T {
        return this.user;
    }

    isAuthenticated() {
        return this.user && this.user.token;
    }

}
